import Vue from 'vue'
import Vuex from 'vuex'
import toastr from 'toastr'
import axios from 'axios'
import support from './support.json'
// import router from '@/router'

Vue.use(toastr)
Vue.use(Vuex)

export const store = new Vuex.Store({
  state: {
    api: {
      user: {},
      groupId: 0
    },
    loading: false,
    loadingTable: false,
    error: null
  },
  actions: {
    clearError ({commit}) {
      commit('clearError')
    },
    loadInitResource ({commit, state}, data) {
      return new Promise((resolve, reject) => {
        let param = {
          params: {
            '_paygateaction_WAR_TichHopGiaoThongportlet_idDebit': data.id,
            '_paygateaction_WAR_TichHopGiaoThongportlet_documentname': data.documentname,
            '_paygateaction_WAR_TichHopGiaoThongportlet_documentyear': data.documentyear,
            '_paygateaction_WAR_TichHopGiaoThongportlet_maritimecode': data.maritimecode
          }
        }
        axios.get(support.renderURLInit, param).then(function (response) {
          let serializable = response.data
          resolve(serializable)
        }, error => {
          reject(error)
        })
      })
    }
  },
  mutations: {
    setLoading (state, payload) {
      state.loading = payload
    },
    setLoadingTable (state, payload) {
      state.loadingTable = payload
    },
    setError (state, payload) {
      state.error = payload
    },
    clearError (state) {
      state.error = null
    }
  },
  getters: {
    loading (state) {
      return state.loading
    },
    loadingTable (state) {
      return state.loadingTable
    },
    error (state) {
      return state.error
    },
    index (state) {
      return state.index
    }
  }
})



